.cv-container {
  max-width: 800px;
  margin: 0 auto;
  padding: 2rem;
  line-height: 1.6;
}

.cv-header {
  text-align: center;
  margin-bottom: 2rem;
}

.cv-header h1 {
  margin: 0;
  font-size: 2.5rem;
}

.cv-header h2 {
  margin: 0.5rem 0;
  color: var(--primary-color);
  font-weight: normal;
}

.contact-info {
  display: flex;
  justify-content: center;
  gap: 1.5rem;
  color: var(--text-secondary);
}

.cv-section {
  margin: 2rem 0;
}

.cv-section h3 {
  border-bottom: 2px solid var(--primary-color);
  padding-bottom: 0.5rem;
  margin-bottom: 1rem;
}

.skills-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 2rem;
}

.education-item,
.experience-item {
  margin-bottom: 1.5rem;
}

.education-item h4,
.experience-item h4 {
  margin: 0;
  color: var(--text-primary);
}

.education-item p,
.experience-item p {
  color: var(--text-secondary);
  margin: 0.25rem 0;
}

ul {
  list-style-type: none;
  padding-left: 0;
}

ul li {
  margin-bottom: 0.5rem;
} 