.about-container {
  max-width: 800px;
  margin: 0 auto;
  padding: 2rem;
}

.about-content {
  line-height: 1.6;
}

.about-content h1 {
  color: var(--primary-color);
  margin-bottom: 2rem;
  text-align: center;
}

.about-section {
  margin-bottom: 2.5rem;
}

.about-section h2 {
  color: var(--text-primary);
  margin-bottom: 1rem;
  font-size: 1.5rem;
}

.about-section p {
  color: var(--text-secondary);
  margin-bottom: 1rem;
}

.about-section ul {
  list-style-type: none;
  padding-left: 0;
}

.about-section ul li {
  color: var(--text-secondary);
  margin-bottom: 0.5rem;
  padding-left: 1.5rem;
  position: relative;
}

.about-section ul li::before {
  content: "â€¢";
  color: var(--primary-color);
  position: absolute;
  left: 0;
}

@media (max-width: 768px) {
  .about-container {
    padding: 1rem;
  }
  
  .about-section h2 {
    font-size: 1.3rem;
  }
} 