.projects-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 2rem;
}

.projects-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 2rem;
  margin-top: 2rem;
}

.project-card-link {
  text-decoration: none;
  color: inherit;
  display: block;
}

.project-card {
  border: 1px solid var(--border-color);
  border-radius: 8px;
  overflow: hidden;
  transition: transform 0.2s, box-shadow 0.2s;
  background: var(--bg-color);
}

.project-card-link:hover .project-card {
  transform: translateY(-5px);
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
}

.project-image img {
  width: 100%;
  height: 400px;
  object-fit: cover;
}

.project-content {
  padding: 1.5rem;
}

.tech-tag {
  display: inline-block;
  background: var(--primary-color);
  color: white;
  padding: 0.25rem 0.75rem;
  border-radius: 15px;
  margin: 0.25rem;
  font-size: 0.875rem;
}

.project-details {
  margin-top: 1rem;
  padding-left: 1.25rem;
}

.project-details li {
  margin-bottom: 0.5rem;
  font-size: 0.9rem;
} 